  renderSkills(category = 'basic') {
    const { intl } = this.props;

    const getTagText = (percent) => {
      if (percent >= 80) {
        return intl.formatMessage({ id: 'skills.tag.confidently' });
      } else if (percent >= 60) {
        return intl.formatMessage({ id: 'skills.tag.decently' });
      } else {
        return intl.formatMessage({ id: 'skills.tag.average' });
      }
    };

    const getTagColor = (percent) => {
      if (percent >= 80) {
        return 'green';
      } else if (percent >= 60) {
        return 'blue';
      } else if (percent >= 30) {
        return 'orange';
      } else {
        return 'red';
      } 
    };

    return (
      <LayoutLib style={{ background: 'white' }}>
        {chunk(skills[category], 3).map((row) => (
          <Row className="skill-row" gutter={32}>
            {row.map((skill) => (
              <Col span={8}>
                <div className="skill">
                  <div 
                    style={{ backgroundImage: `url(${skill.icon})` }}
                    className="skill-icon" 
                  />
                  <div className="skill-progress">
                    <b>{skill.name}</b>
                    <div className="skill-tag">
                      <Tag color={getTagColor(skill.percent)}>
                        {getTagText(skill.percent)}
                      </Tag>
                    </div>
                    <Progress showInfo={false} percent={skill.percent} size="small" />
                  </div>
                </div>
              </Col>
            ))}
          </Row>
        ))}
      </LayoutLib>
    );
  }

  renderProjectCards() {
    const { intl } = this.props;

    return projectCards.map((project) => {
      const actions = project.links
        .filter((link) => link)
        .map((link, id) => (
          <a target="_blank" href={link}>
            <Icon type={id === 0 ? 'github' : 'link'} key={id} />
          </a>
        ));

      return (
        <>
          <Card
            hoverable
            style={{ width: 240 }}
            cover={<img 
              alt={intl.formatMessage({ id: `projects.${project.name}.title` })}
              src={project.image} 
            />}
            onClick={() => this.setProjectModal(project.name)}
            actions={actions}
          >
            <Meta 
              title={
                <div style={{ fontWeight: 'bold' }}>
                  {intl.formatMessage({ id: `projects.${project.name}.title` })}
                </div>
              }
              description={intl.formatMessage({ id: `projects.${project.name}.description` })} 
            />
          </Card>
        </>
    )
    });
  }

